export class AssignmentStatus extends React.Component {
    render() {
        var status = this.props.status
        if (status === "approved") {
            return <FinishedAssignmentIcon />
        }
        else if (status === "failed") {
            return <FailedAssignmentIcon />
        }
        else if (status === "submitted") {
            return <PendingAssignmentIcon />
        }
        else {
            return <QuestionIcon />
        }
    }
}

export async function getAssignmentResults() {
    var assignmentResults = []
    await axios.get("https://cambio-api.herokuapp.com/api/AssignmentChecklist/", { headers: { 'Authorization': `token ${token}` } }).then(
        res => {
            assignmentResults = res.data
        }
    ).catch(err => console.log(err))
    return assignmentResults
}

export async function getAssignmentById(id) {
    const config = {
        headers: {
            'Content-Type': 'application/json',
        },
    };
    await axios.post("https://cambio-api.herokuapp.com/api/auth/login", { username: "torgeir", password: "lysen123" }, config).then(res => {
        token = res.data.token
        //alert(token)
    }
    )
    var assignment = {}
    await axios.get(`https://cambio-api.herokuapp.com/api/AssignmentUpload/${id}/`, {headers: {'Authorization': `token ${token}`}}).then(res => {
        assignment = res.data
    })
    //alert(JSON.stringify(assignment))
    return assignment
}

function openInNewTab(href) {
    Object.assign(document.createElement('a'), {
        target: '_blank',
        href: href,
    }).click();
}
var token

export class ChallengeSubmissionOverview extends React.Component {
    constructor(props) {
        super(props)
        this.state = { assignments: [], currentSubmission: null }
        this.getAssignments()
    }
    getAssignments = async () => {
        const config = {
            headers: {
                'Content-Type': 'application/json',
            },
        };
        axios.post("https://cambio-api.herokuapp.com/api/auth/login", { username: "torgeir", password: "lysen123" }, config).then(res => {
            token = res.data.token
            //alert(token)
        }
        )
        await axios.post("https://cambio-api.herokuapp.com/api/auth/login", { username: "torgeir", password: "lysen123" }, config).then(res => {
            token = res.data.token
            //alert(token)
        }
        )
        var assignments = await getAssignmentResults()
        this.setState({ assignments: assignments })
    }
    render() {
        if (this.state.currentSubmission) {
            return <SignleSubmissionOverview submission={this.state.currentSubmission} />
        }
        
        return <>{this.state.assignments.map(assignment => 
        <div style={{ padding: "10px", cursor: "pointer" }}
        onClick={() => this.setState({currentSubmission: assignment})}
        >
            {assignment.challenge_name} {assignment.student}
        </div>
        )}</>
    }
}

class SignleSubmissionOverview extends React.Component {
    constructor(props) {
        super(props)
        this.state = {submission: this.props.submission, submittedLinks: []}
        this.getSubmittedLinks()
    }
    updateAssignmentChecklist = async (newState) => {
        //add patch request later
        this.setState(newState)
    }
    getSubmittedLinks = async () => {
        
        var links = this.state.submission.assignment_id_list
        
        var submittedLinks = [];
        for (var i = 0; i < links.length; i++) {
            var assignment = await getAssignmentById(links[i])
            if (assignment.url_submissions) {
                submittedLinks.push({name: assignment.file_name, url: assignment.url_submissions}) 
            }
            else {
                submittedLinks.push({name: assignment.file_name, url: assignment.file})
            }
        }
        this.setState({submittedLinks: submittedLinks})
    }
    render() {
        var submission = this.state.submission
        return (
        <div>
            {//JSON.stringify(submission)
            }
            <h1>{submission.challenge_name}</h1>
            <div>
                Status <div style={{backgroundColor: "#f8f8f8", borderRadius: "10px", width: "105px", overflow: "hidden"}}>
                    {["approved", "failed", "submitted"].map((status, index) => 
                    <div style={{borderLeft: (index === 0 ? null: "1px solid grey"), display: "inline-block", padding: "5px",
                    backgroundColor: (status === submission.status ? "grey":null ), cursor: "pointer"
                    }}
                    onClick={() => {
                        var newState = this.state.submission
                        newState.status = status
                        this.updateAssignmentChecklist(newState)
                    }}
                    >
                        <AssignmentStatus status={status}/>
                    </div>
                    )}
                    </div>
                    <div style={{marginTop: "20px"}} >
                        Submitted links:
                        {this.state.submittedLinks.map( assignment => {
                            //var assignment = await getAssignmentById(assignmentId)
                           //return assignmentId
                            return (
                            <div style={{cursor: "pointer"}}
                            onClick={() => openInNewTab(assignment.url)}
                            >{assignment.name}</div>
                        )
                        }
                    )}
                    </div>
                    <div>
                        <input onChange={(event) => {
                            var newState = this.state.submission
                            newState.comment = event.target.value
                            this.updateAssignmentChecklist({submission: newState})
                        }} placeholder="comment" value={submission.comment}></input>
                    </div>
                    <div>
                </div>
            </div>
        </div>)
    }
}